.chatPage{
    background-color: rgb(31, 29, 29);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chatContainer{
    background-color: white;
    height: 60%;
    width: 50%;
}

.chatHeader{
    background-color: crimson;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chatHeader >h2{
    font-size: 1.5vmax;
    color: white;
    margin: 1.5vmax;
}

.chatHeader img{
    color: white;
    width:25px;
    height: 25px;
    margin: 2.5vmax;
    transition: all 0.3s;
    cursor: pointer;
}

.chatHeader img:hover{
    transform: scale(1.2);
}

.chatBox{
    /* border: 0.2vmax solid black; */
    height: 70%;
    box-sizing: border-box;
    
}

.inputBox{
    /* border:0.2vmax solid black; */
   
    height: 15%;
    box-sizing: border-box;
    display: flex;
}

#chatInput{
    
    width: 80%;
    border: none;
    padding: 2vmax;
    background-color: white;
    box-sizing: border-box;
    outline: none;
    font-size: 1.2vmax;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    border-top: 0.2vmax solid black;
}

.sendBtn{
    background-color: crimson;
    color: white;
    border: none;
    width: 20%;
    transition:all 0.3s ;
    
}

.sendBtn > img{
    width: 2.6vmax;
    filter: brightness(100) invert();
    transition: 0.3s;
}

.sendBtn:hover{
    background-color: rgb(114, 20, 40);
}

.sendBtn:hover img{
    transform: translateX(10px);
}