.joinPage{
    background-color: rgb(34, 30, 30);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.joinContainer{
    /* border: 2px solid  white; */
    height: 50%;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.joinContainer > img{
    width: 10vmax;
}

.joinContainer > h1{
    color: white;
    font-size: 3vmax;
    border-bottom: 0.2vmax solid white;
    text-align: center;
    padding: 1vmax;
    font-family: "Roboto";
}

#joinInput{
    box-sizing: border-box;
    width: 25vmax;
    padding: 1.5vmax;
    border:none;
    outline:none;
    font-size: 1.2vmax;
    margin: 2vmax;
    box-sizing: border-box;

}

.joinBtn{
    box-sizing: border-box;
    width: 25vmax;
    padding: 1.5vmax;
    border:none;
    outline:none;
    font-size: 1.2vmax;
    color: white;
    background-color: crimson;
    
    font-family: "Roboto";
    transition: all 0.3s;
    cursor: pointer;

} 

.joinBtn:hover{
    background-color: rgb(77, 4, 19);
    color: white;
}